<template>
    <div class="guanwang">
        <img width="100%" src="https://www.xingnanzhuli.com/guanwang.png" />
        <div class="dibu">
            <a href="http://www.beian.miit.gov.cn" target="_blank">备案号：桂ICP备18010588号-1</a>
        </div>
    </div>
</template>

<script>
    export default {
        async mounted() {
            this.screenWidth=document.body.clientWidth;
            this.xiaochengxumaobj.top=1920/this.screenWidth*16+'px';
            this.andoridobj.top=1920/this.screenWidth*16+'px';
        },
        async created() {},
        data() {
            return {
                screenWidth:0,
                xiaochengxumaobj:{
                    position: 'absolute',
                    width:'10%',
                    left:'70%',
                    top:'0%',
                },
                andoridobj:{
                    position: 'absolute',
                    width:'10%',
                    left:'48%',
                    top:'0%',
                },
            }
        },
        methods: {},
    }
</script>

<style lang="scss">
    .guanwang {
        .dibu {
            text-align: center;
            margin-bottom: 50px;
        }
    }
</style>



